import Vue from 'vue'
import Router from 'vue-router'
import MiracleDapp from '@/components/miracle-dapp'
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    name: 'miracle-dapp',
    component: MiracleDapp
  }]
})



