import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
// Ready translated locale messages
const messages = {
    sg: require('./lang/sg'),
    en: require('./lang/en'),

//   zh: {
//     message: {
//       hello: '嗨，世界',
//     }
//   },
//   en: {
//     message: {
//       hello: 'hello world',
//     }
//   },
//   ja: {
//     message: {
//       hello: 'こんにちは、世界'
//     }
//   }
}
export default new VueI18n({
  locale: 'sg', // set locale
  messages, // set locale messages
})


