<template>
    <!-- info feature begin-->
    <div class="joinbox">
      <div class="container">
        <div class="row">
          <div class="col-sm-12 col-md-8 offset-md-2">
            <ul class="nav nav-tabs jointab" role="tablist">
              <li role="presentation">
                <a href="javascript:void(0)" v-on:click="tab(1)" v-bind:class="{'active': tabNo==1}">{{ $t("joinbox.tab01") }}</a>
              </li>
              <li role="presentation">
                <a href="javascript:void(0)" v-on:click="tab(2)" v-bind:class="{'active': tabNo==2}">{{ $t("joinbox.tab02") }}</a>
              </li>
              <li role="presentation">
                <a href="javascript:void(0)" v-on:click="tab(3)" v-bind:class="{'active': tabNo==3}">{{ $t("joinbox.tab03") }}</a>
              </li>
            </ul>
            <div class="tab-content">
              <div role="tabpanel" class="tab-pane active" id="tab01">
                <div class="row rulelist">
                  <div class="rulelistbox col-sm-12">
                    <ul class="list-inline" >
                      <li class="list-inline row" v-for="(item,index) in tabList" v-bind:key='index'>
                        <div class="col-7 text-left" >{{item.name}}</div>
                        <div class="col-5 text-right"><span>{{item.value}}</span></div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
                <div class="row"><br>
                    <div class="col-md-6 offset-md-3">
                        <button type="button" class="btn btn-outline-primary btn-block withdrawBtn" v-on:click="cleanBonus" >{{ $t("joinbox.clean") }}</button>
                    </div>
                </div>
              <div class="row"><br>
                <div class="col-md-6 offset-md-3">
                    <button type="button" class="btn btn-outline-primary btn-block withdrawBtn" v-on:click="cash" >{{ $t("joinbox.cash") }}</button>
                </div>
              </div>
              <div class="row"><br>
                <div class="col-md-6 offset-md-3">
                  <button type="button" class="btn btn-outline-primary btn-block withdrawBtn" v-clipboard:copyhttplist="httpUrl" v-clipboard:success="onCopy">{{ $t("joinbox.recommend") }}</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


</template>

<script>
import { connect } from "tls";
import { fail } from 'assert';
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'

export default {
    props: ['message'],
    name: "miracle-data",
    computed: {
        ...mapState({
            isInjected: state => state.web3.isInjected,
            network: state => NETWORKS[state.web3.networkId],
            networkId: state => state.web3.networkId,
            coinbase: state => state.web3.coinbase,
            balance: state => state.web3.balance,
            httpUrl: state => '://'+document.domain+'?code='+state.web3.coinbase,
            ethBalance: state => {
            if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
            }    
        }),
      shouyiList() {
        return [
            {'name':this.$t("joinbox.Onelist01"),'value':'---'},
            {'name':this.$t("joinbox.Onelist02"),'value':'---'},
            {'name':this.$t("joinbox.Onelist05"),'value':'---'},
            {'name':this.$t("joinbox.Onelist04"),'value':'---'},
            {'name':this.$t("joinbox.Onelist03"),'value':'---'},
            {'name':this.$t("joinbox.Onelist06"),'value':'---'},
            {'name':this.$t("joinbox.Onelist07"),'value':'---'},
            {'name':this.$t("joinbox.Onelist08"),'value':'---'}
            
        ]
      },
      yejiList() {
        return [
            {'name':this.$t("joinbox.Twolist01"),'value':'---'},
            {'name':this.$t("joinbox.Twolist02"),'value':'---'},
            {'name':this.$t("joinbox.Twolist03"),'value':'---'}
        ]
      },
      dataList() {
        return [
            {'name':this.$t("joinbox.Threelist01"),'value':'---'},
            // {'name':this.$t("joinbox.Threelist02"),'value':'---'},
            {'name':'V1 人数','value':'---'},
            {'name':'V2 人数','value':'---'},
            {'name':'V3 人数','value':'---'},
            {'name':'V4 人数','value':'---'}
        ]
      }
    },
    data() {
        return {
            tokenAlert:false,
            tabList:{},
            tabNo:1,
            levels:['--','银卡','金卡','砖卡'],
            VIPS:['--','V1','V2','V3'],
            myFee:0
        };
    },
    created: function() {
        // this.getUser();
        // this.getLevels();
        this.tabList = this.shouyiList;
    },
    watch:{
        shouyiList(){
            if(this.tabNo==1){
                this.tabList = this.shouyiList;
            }
        },
        yejiList(){
            if(this.tabNo==2){
                this.tabList = this.yejiList;
            }
        },
        dataList(){
            if(this.tabNo==3){
                this.tabList = this.dataList;
            }
        }
    },
    methods: {
        onCopy(e) {
            this.$toastr.success('复制成功');
        },
        tab(data){
            if(data==1){
                this.tabNo = 1;
                this.tabList = this.shouyiList;
            }else if(data==2){
                this.tabNo = 2;
                this.tabList = this.yejiList;
            }else{
                this.tabNo = 3;
                this.tabList = this.dataList;
            }
        },
        cleanBonus(event) {
            this.winEvent = null;
            this.pending = true;
            this.$store.state.contractInstance().everydayBonus({
                gas: 300000,
                value: this.$store.state.web3.web3Instance().toWei(this.amount, "ether"),
                from: this.$store.state.web3.coinbase
            },(err, result) => {
            if (err) {
                console.log(err);
                this.pending = false;
            } else {
                let Won = this.$store.state.contractInstance().Won();
                    Won.watch((err, result) => {
                    if (err) {
                        console.log("could not get event Won()");
                    } else {
                        console.log(err);
                        console.log(result);
                    }
                    });
                }
            });
        },
        
        getUser(event) {
            this.$store.state.contractInstance().getUser({ from: this.$store.state.web3.coinbase },
            (err, result) => {
                if (err) {
                    console.log(Number(err));
                } else {
                    console.log(result);
                    this.shouyiList[0].value = isNaN(Number(result[0]))?'--':this.levels[Number(result[0])];
                    this.shouyiList[1].value = this.VIPS[Number(result[1])];
                    this.shouyiList[2].value = this.fromWei(result[2]) +' ETH';
                    this.shouyiList[3].value = this.fromWei(result[3]) +' ETH';
                    this.shouyiList[4].value = this.fromWei(result[4]) +' ETH';
                    this.shouyiList[5].value = this.fromWei(result[5]) +' ETH';
                    this.shouyiList[6].value = this.fromWei(result[6]) +' ETH'; // Fee
                    this.shouyiList[7].value = this.fromWei(result[7]) +' ETH'; //TouziGangGanFee

                    this.myFee = result[6];
                }
            });

            this.$store.state.contractInstance().getUserData(
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        console.log(result);
                        this.yejiList[0].value = Number(result[0]);
                        this.yejiList[1].value = this.fromWei(result[1]) +' ETH';
                        this.yejiList[2].value = this.fromWei(result[2]) +' ETH';
                    }
                }
            );
            this.$store.state.contractInstance().getGuDong(
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        // this.dataList[0].value = 300;
                        for(var i=0;i<=4;i++){
                            this.dataList[i].value = Number(result[i]);
                        }
                    }
                }
            );
        },
        fromWei(data){
            return data>0?this.$store.state.web3.web3Instance().fromWei(data,'ether'):0;
        },
        getLevels(event) {
            this.$store.state.contractInstance().getLevels(1,
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                    console.log(err);
                    } else {
                    console.log(err);
                    console.log(Number(result[0]));
                    console.log(result);
                    this.number = result;
                    }
                }
            );
        },
        cash(){
            if(this.myFee<=0){
                this.$toastr.warning("账户余额足无法提现");
                return false;
            }
            this.$store.state.contractInstance().cash({
                gas: 300000,
                value: '0',
                from: this.$store.state.web3.coinbase
            },(err, result) => {
                if (err) {
                    console.log(err);
                    this.pending = false;
                } else {
                    let Won = this.$store.state.contractInstance().Won();
                    Won.watch((err, result) => {
                        if (err) {
                            console.log("could not get event Won()");
                        } else {
                            console.log(result);
                        }
                    });
                }
            });
        }
    },
    mounted() {
        this.$store.dispatch("getContractInstance");
        var _this = this;

        setTimeout(function() {
            // if(_this.networkId != 1){
            //     _this.$toastr.error("請先登錄 Metamask 或 trustwallet！");
            //     return false;
            // }
            _this.getUser();
        }, 1000);
    }
};
</script>

<style scoped>
.loginCan {
  position: absolute;
  top: 100px;
  left: 0;
  width: 100%;
  box-sizing: border-box;
}
</style>




