<template>
  <div class="casino">
    <!-- preloader begin-->
    <div class="preloader" style="display:none">
      <div class="loader"><hr><hr></div>
    </div>
    <!-- preloader end -->

    <!-- header begin-->
    <div class="header">
      <div class="container">
        <div class="row justify-content-between">
          <div
            class="col-xl-2 col-lg-2 d-xl-flex d-lg-flex d-block align-items-center mobile-header"
          >
            <div class="row d-flex">
              <div class="col-xl-12 col-lg-12 col-6 d-flex align-items-center">
                <div class="logo">
                  <a href="index.html">
                    <img src="img/logo.png" alt>
                  </a>
                </div>
              </div>
              <div class="col-xl-12 col-6 d-xl-none d-lg-none d-block">
                <button class="navbar-toggler collapsed iconfont icon-webicon03"  type="button" v-on:click="menuTag">
                </button>
              </div>
            </div>
          </div>
          <div class="justify-content-end min-height-none">
            <div class="main-menu">
              <nav class="navbar navbar-expand-lg">
                <div class="navbar-collapse collapse " v-bind:class="{'show': menuShow}" id="navbarNavAltMarkup" style>
                  <ul class="navbar-nav nav">
                    <li class="nav-item">
                      <a class="nav-link" href="javascript:void(0)" data-i18n="button.english" v-on:click="tag('en')" id="en">{{ $t("button.english") }}</a>
                    </li>
                    <li class="nav-item">
                      <a class="nav-link" href="javascript:void(0)" data-i18n="button.singapore" v-on:click="tag('sg')" id="sg">{{ $t("button.singapore") }}</a>
                    </li>
                  </ul>
                </div>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- banner begin-->
    <div class="banner" style="background:#000C11;">
      <div class="container">
        <div class="justify-content-center">
          <div class="banner-content">
            <h1 class="bounceInDown animated">Miracle</h1>
            <div class=" bounceInDown animated" ref="number"></div>

          </div>
        </div>
      </div>
      <div class="banner-star"></div>
      <!-- <canvas id="myCanvas"></canvas> -->
      <canvas ref="loginCanv" class="loginCan"></canvas>
      <canvas ref="loginCanvT" class="loginCan"></canvas>
    </div>
    <!-- banner end -->

    <!-- join begin-->
    <div class="feature">
      <div class="container">
        <div class="row">
          <div class="col-sm-12 col-md-6 offset-md-3">
            <div class="mb-3 text-center">
              <h2 class="card-title text-center mt-1">
                <span data-i18n="feature.h2">{{ $t("feature.h2") }}</span>
                <br>
                <span class="c-blue">300SIN/ETH</span>
              </h2>
              <form class="tabform">
                <div class="input-group mb-3">
                  <input type="text" v-model="amount" class="form-control" min="1" placeholder="1 ETH" aria-label="1 ETH" aria-describedby="tab01eth" id="SendethNum">
                  <div class="input-group-append">
                    <span class="input-group-text" id="tab01eth">ETH</span>
                  </div>
                </div>
                <div class="tabform-btnbox">
                  <span class="btn btn-outline-secondary btn-lg" v-bind:class="{'active': amount==1}" v-on:click="buyNumber(1)">
                    <span class="num">1</span> ETH
                  </span>
                  <span class="btn btn-outline-secondary btn-lg" v-bind:class="{'active': amount==11}" v-on:click="buyNumber(11)">
                    <span class="num" >11</span> ETH
                  </span>
                  <span class="btn btn-outline-secondary btn-lg" v-bind:class="{'active': amount==21}" v-on:click="buyNumber(21)">
                    <span class="num">21</span> ETH
                  </span>
                </div>
                <button type="button"  v-on:click="addOrder" class="btn btn-lg btn-block btn-primary tabbuy">
                  <span data-i18n="feature.send">{{ $t("feature.send") }}</span> ETH
                </button>
                <button type="button" class="btn btn-outline-primary btn-block mt-3" v-on:click="tokenModal">{{ $t("feature.t01") }}{{ $t("feature.t02") }}</button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- join end -->
    <!-- info feature begin-->
    <mitacle-data/>

    <!--彈出令牌-->
    <div class="modal fade" v-bind:class="{'show':tokenAlert}"  v-show="tokenAlert" id="GameModal" style="display:block">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="GameModalTitle" data-i18n="GameModal.title">{{ $t("feature.t02") }}</h5>
            <button type="button" class="close" v-on:click="tokenModal('close')" >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <h2 class="card-title text-center">
              <span class="c-blue">1ETH=3000SIN</span>
            </h2>
            <form class="tabform">
              <div class="input-group mb-3">
                <input type="number" class="form-control" placeholder="1 ETH" aria-label="1 ETH" v-model="paySIN" @input="change" @change="change">
                <div class="input-group-append">
                  <span class="input-group-text" id="tab02eth"><span id="totalpcc">{{EthToSIN}}</span> SIN</span>
                </div>
              </div>
              <div class="input-group">
                    <button type="button" class="btn submit-btn btn-block tabbuy"  v-on:click="BuySIN"  data-i18n="GameModal.buy">{{ $t("GameModal.buy") }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop fade show" v-show="tokenAlert" ></div>
  </div>
</template>

<script>
import MiracleData from '@/components/miracle-data'
import { connect } from "tls";
import { fail } from 'assert';
export default {
    name: "miracle-dapp",
    data() {
        return {
            menuShow:false,
            tokenAlert:false,
            amount: 1,
            pending: false,
            winEvent: null,
            // oldNum:'',
            EthToSIN:30,
            paySIN:0.01,
            MySIN:0, //我的SIN余额
            burnRatio:300 //焚毁比例
            ,TotalFee:'000000'
            ,html: ''
        };
    },
    beforeCreate () {
        this.$store.dispatch('registerWeb3')
    },
    computed: {
        inpNum () {
            return this.oldNum
        }
    },
    created: function() {
        // this.getUser();
        // this.getLevels();
        this.tabList = this.shouyiList;
    },
    components: {
        'mitacle-data': MiracleData
    },
    methods: {
        menuTag(){
            if(this.menuShow){
                this.menuShow = false;
            }else{
                this.menuShow = true;
            }
        },
        tag (res) {
            this.menuShow = false;
            if (res === 'sg') {
                this.$i18n.locale = 'sg';
            } else {
                this.$i18n.locale = 'en';
            }  
        },
        buyNumber(event){
            this.amount=event;
        },
        change (event) {
            let val = event.target.value.trim()
            if (/^\d+(\.\d{1,4})?$/.test(val)) {
                this.paySIN = val
            } else {
                this.paySIN = ''
            }
            this.EthToSIN = this.paySIN * 3000
            console.log()
        },
        tokenModal(res){
            if(res=='close'){
                this.tokenAlert = false;
            }else{
                this.tokenAlert = true;
            }
        },
        tab(data){
            if(data==1){
                this.tabNo = 1;
                this.tabList = this.shouyiList;
            }else if(data==2){
                this.tabNo = 2;
                this.tabList = this.yejiList;
            }else{
                this.tabNo = 3;
                this.tabList = this.dataList;
            }
        },
        addOrder(event) {
            if(!this.$route.query.code){
                this.$toastr.warning("推荐码不存在");
                return false;
            }
            if(!this.$store.state.web3.coinbase){
                this.$toastr.error("請先登錄 Metamask 或 trustwallet！");
                return false;
            }
            console.log(this.MySIN);
            var amountPrice = this.$store.state.web3.web3Instance().toWei(this.amount, "ether");
            console.log(Number(this.burnRatio));
            if(this.MySIN ==0 || this.MySIN < amountPrice * this.burnRatio){
                this.$toastr.error("你的令牌不足 "+parseInt(this.amount * this.burnRatio, 10)+" SIN");
                return false;
            }
            // console.log("BETTING ON NUMBER, AMOUNT", event.target.innerHTML, this.amount, this.$store.state.web3.coinbase);

            this.winEvent = null;
            this.pending = true;
            this.$store.state.contractInstance().addOrder(
                this.$route.query.code,{
                    gas: 1000000,
                    value: this.$store.state.web3.web3Instance().toWei(this.amount, "ether"),
                    from: this.$store.state.web3.coinbase
                },(err, result) => {
                if (err) {
                    console.log(err);
                    this.pending = false;
                } else {
                    let Won = this.$store.state.contractInstance().OrderWon();
                    Won.watch((err, result) => {
                        if (err) {
                            console.log("could not get event OrderWon()");
                        } else {
                            console.log(result);
                            // this.winEvent = result.args
                            // this.winEvent._amount = parseInt(result.args._amount, 10)
                            // this.pending = false
                        }
                    });
                }
                }
            );
        },
        eventTicketsBought: function () {
            let self = this;
            var event = this.$store.state.contractInstance().TicketsBought({},{fromBlock: (this.currentLotteryBlock+1), toBlock: 'latest'});
            event.watch(function(error, result){
                if (error) {
                    console.log(error);
                } else {
                    //console.log(result);
                    self.ticketsIssued[result.transactionIndex] = {
                        'address' : result.args._from,
                        'quantity' : parseInt(result.args._quantity, 10),
                        'name' : self.getAddressName(result.args._from),
                    };
                }
            });
        },
        BuySIN(event) {
            if(!this.$route.query.code){
                this.$toastr.warning("推荐码不存在");
                return false;
            }
            if(!this.$store.state.web3.coinbase){
                this.$toastr.error("請先登錄 Metamask 或 trustwallet！");
                return false;
            }
            // console.log("BETTING ON NUMBER, AMOUNT", event.target.innerHTML, this.amount, this.$store.state.web3.coinbase);

            this.winEvent = null;
            this.pending = true;
            this.$store.state.contractInstanceSIN().buy({
                gas: 300000,
                value: this.$store.state.web3.web3Instance().toWei(this.paySIN, "ether"),
                from: this.$store.state.web3.coinbase
            },(err, result) => {
                if (err) {
                    this.pending = false;
                } else {
                    // let Won = this.$store.state.contractInstance().Won();
                    // Won.watch((err, result) => {
                    //     if (err) {
                    //         // console.log("could not get event Won()");
                    //     } else {
                    //         // console.log(err);
                    //         // console.log(result);
                    //         // this.winEvent = result.args
                    //         // this.winEvent._amount = parseInt(result.args._amount, 10)
                    //         // this.pending = false
                    //     }
                    // });
                }
            });
        },
        getSIN(){
            this.$store.state.contractInstanceSIN().balanceOf(this.$store.state.web3.coinbase,
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        this.MySIN = result;
                    }
                }
            );
            this.$store.state.contractInstanceSIN().burnRatio(
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        this.burnRatio = result;
                        // console.log(Number(result))
                    }
                }
            );
            this.$store.state.contractInstance().TotalFee(
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        if(Number(result)>0){
                            this.TotalFee = Number(this.fromWei(Number(result))) * 2  + 1500;
                            this.html = '';
                            this.initNum();
                        }
                        
                    }
                }
            );
        },
        fromWei(data){
            return data>0?this.$store.state.web3.web3Instance().fromWei(data,'ether'):0;
        },
        getUser(event) {
            this.$store.state.contractInstance().getUser(
                this.$store.state.web3.coinbase,
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        // console.log(err);
                        // console.log(Number(result[0]))
                        // console.log(result);
                        // this.number = result
                    }
                }
            );
        },
        getLevels(event) {
            this.$store.state.contractInstance().getLevels(1,
                { from: this.$store.state.web3.coinbase },
                (err, result) => {
                    if (err) {
                        console.log(err);
                    } else {
                        this.number = result;
                    }
                }
            );
        },
        ani() {
            let c = this.$refs.loginCanv.getContext("2d");
            let postctx = this.$refs.loginCanvT.getContext("2d");
            let canvas = c.canvas;
            let vertices = [];
            // Effect Properties
            let vertexCount = 8000;
            let vertexSize = 3;
            let oceanWidth = 204;
            let oceanHeight = -80;
            let gridSize = 32;
            let waveSize = 16;
            let perspective = 100;
            // Common variables
            let depth = (vertexCount / oceanWidth) * gridSize;
            let frame = 0;
            let { sin, cos, tan, PI } = Math;
            // Render loop
            let loop = () => {
                let rad = (sin(frame / 100) * PI) / 20;
                let rad2 = (sin(frame / 50) * PI) / 10;
                frame++;
                if ( postctx.canvas.width !== postctx.canvas.offsetWidth || postctx.canvas.height !== postctx.canvas.offsetHeight ) {
                    postctx.canvas.width = canvas.width = postctx.canvas.offsetWidth;
                    postctx.canvas.height = canvas.height = postctx.canvas.offsetHeight;
                }
                c.fillStyle = `hsl(200deg, 100%, 2%)`;
                c.fillRect(0, 0, canvas.width, canvas.height);
                c.save();
                c.translate(canvas.width / 2, canvas.height / 2);
                c.beginPath();
                vertices.forEach((vertex, i) => {
                    let ni = i + oceanWidth;
                    let x = vertex[0] - (frame % (gridSize * 2));
                    let z =
                        vertex[2] -
                        ((frame * 2) % gridSize) +
                        (i % 2 === 0 ? gridSize / 2 : 0);
                    let wave =
                        cos(frame / 45 + x / 50) -
                        sin(frame / 20 + z / 50) +
                        sin(frame / 30 + (z * x) / 10000);
                    let y = vertex[1] + wave * waveSize;
                    let a = Math.max(0, 1 - Math.sqrt(x ** 2 + z ** 2) / depth);
                    let tx, ty, tz;
                    y -= oceanHeight;
                    // Transformation variables
                    tx = x;
                    ty = y;
                    tz = z;
                    // Rotation Y
                    tx = x * cos(rad) + z * sin(rad);
                    tz = -x * sin(rad) + z * cos(rad);
                    x = tx;
                    y = ty;
                    z = tz;
                    // Rotation Z
                    tx = x * cos(rad) - y * sin(rad);
                    ty = x * sin(rad) + y * cos(rad);
                    x = tx;
                    y = ty;
                    z = tz;
                    // Rotation X
                    ty = y * cos(rad2) - z * sin(rad2);
                    tz = y * sin(rad2) + z * cos(rad2);
                    x = tx;
                    y = ty;
                    z = tz;
                    x /= z / perspective;
                    y /= z / perspective;
                    if (a < 0.01) return;
                    if (z < 0) return;
                    c.globalAlpha = a;
                    c.fillStyle = `hsl(${180 + wave * 20}deg, 100%, 50%)`;
                    c.fillRect(
                        x - (a * vertexSize) / 2,
                        y - (a * vertexSize) / 2,
                        a * vertexSize,
                        a * vertexSize
                    );
                    c.globalAlpha = 1;
                });
                c.restore();
                // Post-processing
                postctx.drawImage(canvas, 0, 0);
                postctx.globalCompositeOperation = "screen";
                postctx.filter = "blur(16px)";
                postctx.drawImage(canvas, 0, 0);
                postctx.filter = "blur(0)";
                postctx.globalCompositeOperation = "source-over";
                requestAnimationFrame(loop);
            };
            // Generating dots
            for (let i = 0; i < vertexCount; i++) {
                let x = i % oceanWidth;
                let y = 0;
                let z = (i / oceanWidth) >> 0;
                let offset = oceanWidth / 2;
                vertices.push([(-offset + x) * gridSize, y * gridSize, z * gridSize]);
            }
            loop();
        },
        initNum () {
            this.TotalFee.toString().split('').forEach(v => {
                this.html += '<span class="counter">' + v + '</span>'
            })
            this.$refs.number.innerHTML = this.html
        }
    },
    mounted() {
        
        this.initNum();
        
        // this.$toastr.success('Message');
        if(!this.$route.query.code){
            this.$toastr.warning("邀请码不存在");
        }
        this.ani();
        // if(!this.$store.state.web3.coinbase){
        //     this.$toastr.error("請先登錄 Metamask 或 trustwallet！");
        //     return false;
        // }
        this.$store.dispatch("getContractInstance");
        var _this = this;
        setTimeout(function() {
            // _this.getUser();
            // _this.getLevels();
            _this.getSIN();

            let Won = _this.$store.state.contractInstance().OrderWon();
            Won.watch((err, result) => {
                if (err) {
                    console.log("could not get event OrderWon()");
                } else {
                    console.log(result);
                    // this.winEvent = result.args
                    // this.winEvent._amount = parseInt(result.args._amount, 10)
                    // this.pending = false
                }
            });

        }, 1000);
    }
};
</script>

<style scoped>
    .loginCan {
        position: absolute;
        top: 100px;
        left: 0;
        width: 100%;
        height: 400px;
        box-sizing: border-box;
    }
    .modal-dialog{margin-top: 15%;}

    @media (max-width: 768px) {
        .loginCan{top: -100px}
    }
</style>




